import React from 'react';
import '../components/Components.css';
import { Typography, Tooltip, Fab, Grow, Slide, } from '@material-ui/core';
import { React as ReactIcon, MaterialUi, FileDownload, } from 'mdi-material-ui';
import Database from '../assets/Database';

const darkCircleFilters = "hue-rotate(-45deg)";
const defaultCircleFilter = "hue-rotate(0)";

// const maxLen = window.innerHeight;

// let longshadowStr = "";
// let i = 0;

// for (i = 0; i < maxLen; i++){
//   longshadowStr += `rgba(90, 90, 90, 1) ${i}px ${i}px,`;
// }

// longshadowStr += `rgba(90, 90, 90, 1) ${i}px ${i}px`;

const HomePage = (props) => {
  const { currentScheme, isDark, mainColor, sectionId } = props;

  return (
    <div 
      id={sectionId}
      className="ReferenceAnchor"
    > 
      <Grow in timeout={3000}>
      <div>
      <Slide in timeout={3000/4} direction="up">
        <div 
          className="HomePageCont" 
          style={{
            filter: isDark ? darkCircleFilters : defaultCircleFilter
          }}
        >
          <div className="HomePageCircle1"/>
          <div className="HomePageCircle2"/>
          <div className="HomePageCircle3"/>
          <div className="HomePageTextMain">
            <Typography 
              variant="button" 
              style={{
                color: "#ffffff",
                opacity: "0.8",
                transition: "all 1s ease", 
                fontSize: "max(1rem, 2vh)"
              }}
            >
              hi, i'm
            </Typography>
            <Typography 
              variant="overline" 
              style={{ 
                fontSize: "max(2rem, 5vh)", 
                lineHeight: "max(2rem, 6vh)", 
                textAlign: "left", 
                opacity: "0.8",
                transition: "all 1s ease",
                mixBlendMode: "overlay",
              }}
            >
              Mozamel<br/><b>Anwary</b>
            </Typography>
            <br/>
            <Typography variant="button" style={{
              color: "#ffffff",
              opacity: "0.8",
              transition: "all 1s ease", 
              fontSize: "max(1.2rem, 2vh)",
              lineHeight: "max(1.5rem, 3vh)",
            }}>
              Software Engineer
            </Typography>
          </div>
        </div>
      </Slide>
      </div>
      </Grow>
      
      <Tooltip disableFocusListener title="Powered by React | MaterialUI" placement="left">
        <div className="BuiltCont">
          <ReactIcon style={{color: currentScheme.bgInv}}/><br/>
          <MaterialUi style={{color: currentScheme.bgInv}}/>
        </div>
      </Tooltip>

      <div className="FAB">
        <Tooltip title="View/Download Resume" placement="left">
          <Fab size="medium" component="a" href={Database["Resume"]} 
          download="_resume_mozamel_anwary" aria-label="DownloadResume"
          style={{color: "white", backgroundColor: mainColor, opacity: "0.8"}}>
            <FileDownload/>
          </Fab>
        </Tooltip>
      </div>
    </div>
  );
}

export default HomePage;